/* Fonts */
h1,h2,h3 {
    margin-bottom: 1rem;
}

/* Section Body changes */
.home-section .section-body {
    padding: 2.5rem;
    margin: 0 auto;
    max-width: 65rem;
}

/* Intro Section */
#intro {
    padding-top: 2.5rem;
    padding-bottom: 10rem;
    color: #fff;
    text-align: center;
    background-color: rgb(255, 82, 82);
}

#intro p {
    font-size: 1.5rem
}

#intro h1 {
    font-size: 2.25rem!important
}

#intro a {
    width: 50%;
    margin: 0 auto
}

/* Skills Section */
#skills {
    margin-top: -10rem;
    text-align: center
}

#skills .box section {
    margin: 2rem 0
}

.skills-heading {
    margin: .5rem!important;
    color: #ff5252
}

#skills p {
    margin: 0;
    padding: 0
}

#skills ul {
    list-style-type: none;
    margin: 0;
    padding: 0
}

/* Footer Section */
#footer {
    background-color: rgb(255, 82, 82);
    padding-top: 7.5rem
}

#footer,
#footer a svg {
    color: #fff;
    text-align: center
}

#footer a svg {
    height: 1.5rem;
    width: 2.25rem;
    padding: .25rem 0;
    margin: 5px;
    border: 2px solid #ff867f;
    border-radius: .25rem;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

#footer a svg:hover {
    color: #ff5252;
    background-color: #fff;
    border-color: #fff
}
.box {
    display: flex;
    flex-direction: row;
    border: 1px solid #e0e0e0;
    border-radius: 1rem;
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, .2);
    background-color: #fff;
}

@media only screen and (max-width:750px) {
    .box {
        flex-direction: column
    }
}
/* Resume Section */
#resume {
    font-size: 1rem
}

#resume h1 {
    font-size: 2em;
}

/* Section body changes */
.resume-section .section-body {
    padding: 0;
    margin: 0; 
    width: 100%;
    height: 100%;
    box-sizing: inherit;
    display: flex;
    flex-direction: row;
}

@media only screen and (max-width: 750px) {
    .resume-section .section-body {
        flex-direction: column;
    }
}

.resume-section-body {
    float: right;
    height: 100%;
    display: block;
}

.resume-section h2 {
    float: left;
    min-width: 25rem;
    padding-bottom: 0;
    margin-bottom: 0;
}

.resume-section-body p {
    margin: 0;
}

.resume-section-item ul {
    padding-left: 1.25rem;
    padding-top: 0;
    margin-top: 0
}

.resume-section-item ul li {
    word-wrap: break-word
}

.resume-section-item h3 {
    margin-bottom: 0;
    color: #ff5252
}

.resume-section-item h4 {
    margin-top: 0;
    margin-bottom: 0
}

.resume-button-link {
    text-decoration: none
}

.resume-button-link:hover {
    text-decoration: none;
    font-size: 1rem
}

.resume-button {
    text-align: left;
    max-width: 40rem;
    margin: 0 auto;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    margin-top: 3rem;
    margin-bottom: -10rem;
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, .2);
    position: relative;
    z-index: 100
}

@media only screen and (max-width:750px) {
    .resume-button {
        text-align: center
    }
}

.resume-button-text {
    margin-left: -15rem;
    margin-top: 3rem
}

@media only screen and (max-width:750px) {
    .resume-button-text {
        margin-left: 0;
        margin-top: -2rem
    }
}

.resume-button:hover {
    cursor: pointer;
    border-color: #ff5252
}
.box article {
    flex-grow: 1;
    flex-basis: 0;
    -ms-flex-positive: 1;
    -ms-flex-preferred-size: 0;
    color: #000;
    display: block;
    padding: 2.5rem;
    border-right: 1px solid #e0e0e0;
    border-bottom: 0 solid #e0e0e0
}

@media only screen and (max-width:750px) {
    .box article {
        border-right: 0 solid #e0e0e0;
        border-bottom: 1px solid #e0e0e0
    }
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* universal classes */
.no-border {
  border: none!important
}
